import React, { Component } from "react";
import "./Player.css";
import * as $ from "jquery";

class Intro extends Component {

    constructor(props) {
        super(props);
        this.state = {
            recommended: [],
        };
    }

    componentDidMount() {
        // Set token
        $.ajax({
            url: "http://musicapi.leanapp.cn/top/playlist",
            type: "GET",
            data: {
                limit: 5,
                cat: "欧美",
            },
            success: data => {
                this.setState({
                    recommended: data.playlists
                });
            }
        });
    }

    render() {
        return (

            <div className="Intro">
                <table className="Intro-list">
                    <thead>
                        <tr>
                            <th style={{ fontSize: "1em" }}>Hot Playlists</th>
                        </tr>
                    </thead>
                    <tbody>

                    {this.state.recommended.map((playlist) => (
                            <tr key={playlist.id} onClick={() => this.props.action(playlist.id)} className="Song-row">
                                <td >{playlist.name}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
                <img className="album-img" src="%PUBLIC_URL%/logo192.png" alt="album-cover" />
            </div>
        );
    }
}

export default Intro;